*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

.navbar{
  display: flex;
  align-items: center;
  width: 100%;
  padding: 1% 10%;
  justify-content: space-between;
  border-bottom: 1px solid grey;
  position: fixed;
  top: 0;
  background-color: white;
}

.logo{
  font-size: 2em;
  color: brown;
}

.list-item{
  display: flex;
  gap: 2em;
  list-style-type: none;
}

.list{
  text-decoration: none;
  cursor: pointer;
  color: brown;
  font-weight: bold;
  padding: 0.5em 1em;
}

.list:hover{
  background-color: rgb(120, 189, 120);
  color: white;
}

.home-page{
  width: 100%;
  min-width: 100vw;
  min-height: 100vh;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.heading{
  color: rgb(120, 189, 120);
}
.listheading{
  text-decoration: none;
  color: brown;
}

.registerpage{
  width: 100%;
  height: 100%;
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5em;
  margin-bottom: 2em;
}

.container{
  width: 80%;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
}

.regheading{
  width: 100%;
  color: brown;
  text-align: center;
  text-transform: uppercase;
}

.formdata{
  display: flex;
}

.firstsection{
  width: 55%;
  display: flex;
  gap: 2em;
  padding: 2em 3em;
  flex-direction: column;
}
.secondsection{
  width: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
}

label{
  font-size: 1.2em;
  color: brown;
}

input{
  padding: 0.5em 1em;
  font-size: 1.2em;
  color: brown;
}

input:focus{
  outline: none;
  border:2px solid brown;
}

.field{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

button{
  width: 40%;
  margin: 0 auto;
  font-weight: bold;
  color: white;
  cursor: pointer;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 1.2em;
  border: none;
  text-align: center;
  background-color: brown;
  border:2px solid transparent;
  transition: all;
  transition-duration: 300ms;
}

button:hover{
  background-color: white;
  color: brown;
  border: 2px solid brown;
}

.userpage{
  min-width: 100vw;
  min-height: 100vh;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 2em;
  margin-top: 5em;
}

.user{
  width: 100%;

  border: 1px solid red;
}